.education {
    padding: 10px 0px;
    margin: 50px 0px;
    margin-left: 120px;
    background-color: rgba(128, 128, 128, 0.101);
  }
  
  .education hr {
    color:white;
    width: 400px;
    margin: 10px auto;
  }
  
  .education h2 {
    font-weight: bold;
    color:white;
  }
  
  .education .vertical-timeline-element-title {
    color:black;
  }
  
  .education .vertical-timeline-element-subtitle {
    color: #1e1e2c;
  }
  .vertical-timeline-element-content .vertical-timeline-element-date {
   font-size: 20px;
    color: white;
    font-weight: 900;
    font-style: normal;
    font-family: 'Times New Roman', Times, serif;
}




@media(max-width:600px){
  .education{
    padding: 0px;
    margin: 0px;
  }
}