.contact {
    margin-left: 130px;
    margin-bottom: 60px;
    padding-top: 50px;
  }
  
  .card0 {
    box-shadow: 0px 4px 8px 0px #757575;
    border-radius: 0px;
  }
  
  .card2 {
    margin: 0px 40px;
  }
  
  .card1 .image {
    padding: 30px;
    margin-top: 30px;
    height: 100%;
    width: 100%;
  }
  .border-line {
    border-right: 1px solid #e0e0e0;
  }
  .line {
    height: 1px;
    width: 45%;
    background-color: #e0e0e0;
    margin-top: 10px;
    margin-left: 5px;;
  }
  
  .or {
    display: flex;
    width: 10%;
    font-weight: bold;
    margin-left: -10px;;
  }
  
  ::placeholder {
    color: #bdbdbd;
    opacity: 1;
    font-weight: 300;
  }
  
  input,
  textarea {
    padding: 10px 12px;
    border: 1px solid lightgray;
    border-radius: 2px;
    margin-top: 5px;
    margin-top: 2px;
    width: 100%;
    color:white;
    font-size: 1rem;
    letter-spacing: 1px;
  }
  
  input:focus,
  textarea:focus {
    box-shadow: none !important;
    outline: 1px solid #138781 !important;
    outline-width: 0px;
  }
  
  button:focus {
    box-shadow: none;
    outline-width: 0px;
  }
  
  .button {
    background-color: #1e1e2c;
    color: white;
    border-radius: 2px;
    margin-top: 15px;
    padding: 10px;
  }
  
  .button:hover {
    background-color: #000;
    color: white;
  }


  @media(max-width:600px){
    .contact{
      padding: 0px;
      margin: 0px;
    }
  }